<template>
  <div style="display: none" id="content">
    <!--  -->
    <div :class="{block: true, mobileBlock: isMobile}"
         v-if="!isMobile || item.mobile"
         v-for="(item, index) in gameList"
         :key="index"
         :style="color2Style(item.color)">
      <div class="innerBlock" @click="goto(index)">
        <div class="name"><span>{{item.name}}</span></div>
        <div v-if="!isMobile" class="desc">{{item.desc}}</div>
      </div>
      <div v-if="item.loading" class="loading">
        <i class="el-icon-loading"></i>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
  #content {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    align-content: stretch;
    .block{
      min-width: 20%;
      flex: 1;
      position: relative;
      cursor: pointer;
      &.mobileBlock{
        min-width: 50%;
        .innerBlock{
          background-color: rgba(0,0,0,0);
          .name{
            color: rgba(0,0,0,.75);
          }
        }
      }
      .innerBlock{
        width: 100%;
        height: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        border: 8px solid transparent;
        background-color: rgba(0,0,0,0.5);
        transition: background-color 300ms, border-color 300ms;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        .name{
          color: rgba(255,255,255,.5);
          transition: color 500ms;
          font-size: 20px;
          font-weight: bold;
          text-align: center;
        }
        .desc{
          margin-top: 20px;
          color: rgba(255,255,255,.25);
          transition: color 500ms;
          font-size: 12px;
        }
        &:hover{
          border-top-color: rgba(255,255,255,0.5);
          border-left-color: rgba(255,255,255,0.3);
          border-right-color: rgba(0,0,0,0.3);
          border-bottom-color: rgba(0,0,0,0.5);
          background-color: rgba(0,0,0,0);
          .name{
            color: rgba(0,0,0,.75);
          }
          .desc{
            color: rgba(0,0,0,.5);
          }
        }
        &:active{
          border-top-color: rgba(0,0,0,0.5);
          border-left-color: rgba(0,0,0,0.3);
          border-right-color: rgba(255,255,255,0.3);
          border-bottom-color: rgba(255,255,255,0.5);
          background-color: rgba(0,0,0,0.3);
          .name{
            color: rgba(0,0,0,.75);
          }
          .desc{
            color: rgba(0,0,0,.5);
          }
        }
      }
      .loading{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(255,255,255,.5);
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: 64px;
        cursor: no-drop;
      }
    }
  }
</style>

<script>
export default {
  name: 'gameMenu',
  data () {
    return {
      isMobile: false,
      loading: false,
      gameList: [
        {
          name: "",
          path: "/minesweeper",
          desc: "",
          color: "#b0b0b0",
          loading: false,
        },
        {
          name: "",
          path: "/snake",
          desc: "WASD",
          color: "#707070",
          loading: false,
        },
        {
          name: "",
          path: "/color",
          desc: "",
          color: "#ff5e52",
          loading: false,
        },
        {
          name: "",
          path: "/link",
          desc: "",
          color: "#ffef6e",
          loading: false,
        },
        {
          name: "",
          path: "/jump",
          desc: "WASD...",
          color: "#6df8ff",
          loading: false,
        },
        {
          name: "Picross",
          path: "/picross",
          desc: "Picross",
          color: "#5dffbd",
          loading: false,
        },
        {
          name: "",
          path: "/digital",
          desc: " - Bulls and Cows",
          color: "#e19bff",
          loading: false,
        },
        {
          name: "",
          path: "/wolf",
          desc: "kill",
          color: "#ffbc64",
          loading: false,
          mobile: true,
        },
        // {
        //   name: "12",
        //   path: "/graduate",
        //   desc: "110900010643",
        //   color: "#ff7381",
        //   loading: false,
        //   mobile: true,
        // },
        {
          name: "",
          path: "/eco",
          desc: "",
          color: "#6285ff",
          loading: false,
        },
        {
          name: "css",
          path: "/cssDraw",
          desc: "css",
          color: "#a6ff5d",
          loading: false,
          mobile: true,
        },
        {
          name: "",
          path: "/tripleDisaster",
          desc: "",
          color: ["135deg", "#ffad51", "#87511e"],
          loading: false,
          mobile: true,
        },
        {
          name: "",
          path: "/magicCube",
          desc: "three.js3-5",
          color: ["135deg", "#fbff00", "#ff6625"],
          loading: false,
        },
        {
          name: "",
          path: "/gangEditor",
          desc: "three.js",
          color: ["135deg", "#eaff00", "#00d220"],
          loading: false,
        },
        {
          name: "",
          path: "/gangCompany",
          desc: "",
          color: ["135deg", "#35e7ff", "#6f39e2"],
          loading: false,
          mobile: true,
        },
        {
          name: "",
          path: "/animal",
          desc: "",
          color: ["135deg", "#ffc162", "#e4417e"],
          loading: false,
          mobile: true,
        },
        // {
        //   name: "CTO",
        //   path: "/road2CTO",
        //   desc: "rpg",
        //   color: ["135deg", "#56ff7b", "#439f89"],
        //   loading: false,
        //   mobile: true,
        // },
        {
          name: "",
          path: "/cain",
          desc: "",
          color: ["135deg", "#F7B3FF", "#FF25E9"],
          loading: false,
          mobile: true,
        },
        // {
        //   name: "",
        //   path: "/guessBK",
        //   desc: "()",
        //   color: ["135deg", "#56ff7b", "#439f89"],
        //   loading: false,
        //   mobile: true,
        // },
        {
          name: "coc",
          path: "/coc",
          desc: "",
          color: ["135deg", "#fff", "#fff"],
          loading: false,
          mobile: true,
        },
        {
          name: "",
          path: "/light",
          desc: "",
          color: ["135deg", "#ccc", "#fff"],
          loading: false,
          mobile: true,
        },
      ]
    }
  },
  mounted(){
    this.isMobile = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
  },
  methods: {
    color2Style (color) {
      if (typeof color === 'string') return {'backgroundColor': color}
      if (Array.isArray(color)) {
        return {'backgroundImage': `linear-gradient(${color.join(',')})`}
      }
      return {}
    },
    goto(index){
      if(!this.loading){
        let page = this.$router.resolve({
          path: this.gameList[index].path
        });
        window.open(page.href, '_blank');
        // 2025116 10:42:53 
        // if(this.gameList[index].newTab){
        //   let page = this.$router.resolve({
        //     path: this.gameList[index].path
        //   });
        //   window.open(page.href, '_blank');
        // } else {
        //   this.gameList[index].loading = true;
        //   this.loading = true;
        //   this.$router.push(this.gameList[index].path, () => {
        //     this.gameList[index].loading = false;
        //     this.loading = false;
        //   });
        // }
      }
    }
  }
}

